*{padding:0;margin:0;box-sizing:border-box;}
#demo{
    word-break:break-all;
}
#div1 {
    position: fixed;
    left:50%;
    transform: translateX(-50%);
    top: 40px;
}
#div1::before{
    content: '';
    position: absolute;
}
#div1::after{
    content:'';
    position: absolute;
}
@media(max-width:500px){
    #demo{
        height:50vh;
        overflow:auto;
    }
    #div2{
        position:relative;
    }
    #div1{
        position: absolute;
        left:50%;
        transform:translateX(-50%);
        top:80px;
    }
}